.container {
  height: 80vh;
  display: flex;
  flex-direction: column;
   align-items: center; 
  justify-content: center; 
  /* justify-content: center; */

}

.inputContainer h1 {
 font-family: 'PT Serif', serif;
  font-size: 3.5rem;
  color: white;
  margin-bottom: 1.5rem;
}

.inputContainer h1 span {
  color: var(--primary-color);
}

.inputContainer div {
  display: flex;
  margin-bottom: 2rem;
}

.inputContainer input {
  width: 100%;
  padding: 0.5rem 0.2rem 0.5rem 0;
  text-indent: 1rem;
  border: none;
  outline: none;
  font-size: 1rem;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;    

}

.inputContainer input::placeholder {
  font-size: 0.9rem;

}

.inputContainer button {
  justify-content: center;
  padding:0 2rem;
  height: 2.8rem;
  margin-left: center;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: bold;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  color: white;
  background-color: var(--primary-color);
  border: none;
  cursor: pointer;
}

/* result */

.result {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
}

.result p {
  color: rgb(255, 255, 255);
  border: 1px solid var(--primary-color);
  padding: 0.5rem 1rem;
  margin-right: 1rem;
}

.result button {
  padding: 0.5rem 1rem;
  color: white;
  cursor: pointer;
  background: var(--primary-color);
  border: none;
}

 .result button.copied {
  background: white;
  color: var(--primary-color);
}

.noData {
  color: white;
  font-size: 1rem;

}

/* background */
@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  background: #000000;
  overflow: hidden;
  z-index: -1;
}

.background li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 9s linear infinite;
}




